
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import {mainRouter} from '@/router/index'

export default {
name: "index",
components: {
Tabbar,
TabbarItem,
Group,
Cell
},
data() {
return {
tabList: mainRouter,
currentPath: ''
}
},
methods: {
obtainPath(path) {
let routerPathArr = path.split('/')
this.currentPath = `/${routerPathArr[1]}`
}
},
created() {
this.obtainPath(this.$route.path)
},
watch: {
$route(v) {
this.obtainPath(v.path)
}
}
}



